/* -------------------------------------------------------------------------
 * COLORS NEUTRAL
 *
 * Neutral colors are the ones without saturation
 * The values here are for sample proposes, consider add your owns */

// Black & White

// @if $f-color__brand-theme-type == "light"
$f-color__neutral-primary: black !default;
$f-color__neutral-secondary: white !default;
$f-color__neutral-accent: ivory !default;

$f-color__neutral-primary-bg: white !default;
$f-color__neutral-secondary-bg: black !default;
$f-color__neutral-accent-bg: gray !default;

@if $f-color__brand-theme-type == "dark" {
  $f-color__neutral-primary: white !default;
  $f-color__neutral-secondary: black !default;
  $f-color__neutral-accent:  DimGray !default;

  $f-color__neutral-primary-bg: black !default;
  $f-color__neutral-secondary-bg: white !default;
  $f-color__neutral-accent-bg: LightGray !default; }

// Neutral color map

$f-colors-neutral: ("primary": $f-color__neutral-primary, "secondary": $f-color__neutral-secondary, "accent": $f-color__neutral-accent, "primary-bg": $f-color__neutral-primary-bg, "secondary-bg": $f-color__neutral-secondary-bg, "accent-bg": $f-color__neutral-accent-bg);
