/* color-transform
 *
 * eg: color-transform($color, 'ultra-light')
 *     color-transform($color, 'default') // will leave color untransformed
 * ============================================================================ */

@function color-transform($_color: white, $_transform: "default") {
  @if $_transform == "pure-light" {
    @return lighten($_color, 50%); }

  @if $_transform == "ultra-light" {
    @return lighten($_color, 20%); }

  @if $_transform == "light" {
    @return lighten($_color, 10%); }

  @if $_transform == "semi-light" {
    @return lighten($_color, 5%); }

  @if $_transform == "semi-dark" {
    @return darken($_color, 5%); }

  @if $_transform == "dark" {
    @return darken($_color, 10%); }

  @if $_transform == "ultra-dark" {
    @return darken($_color, 20%); }

  @if $_transform == "pure-dark" {
    @return darken($_color, 50%); }

  @if $_transform == "opaque" {
    @return rgba(red($_color), green($_color), blue($_color), 1); }

  @if $_transform == "semi-opaque" {
    @return rgba(red($_color), green($_color), blue($_color), 2 / 3); }

  @if $_transform == "semi-transparent" {
    @return rgba(red($_color), green($_color), blue($_color), 1 / 3); }

  @if $_transform == "transparent" {
    @return rgba(red($_color), green($_color), blue($_color), 0); }

  @return $_color; }
